.input-component {
	display: flex;

	width: 100%;
	height: 55px;

	margin-bottom: 15px;
	padding: 10px;

	border: 1px solid;
	border-radius: 5px;
	border-color: #323e42;

	font-family: 'Indie Flower', cursive;
	font-size: 20px;
	color: #323e42;
	font-weight: bold;

	background-color: #fff;
	box-shadow: 12px 15px 10px -5px #718b93;

	transition: color ease-in-out 0.3s, border-color ease-in-out 0.3s, box-shadow ease-in-out 0.2s,
		left ease-in-out 0.2s;
}

.input-icon {
	margin: 0 10px;
}

.debounce-input {
	max-width: calc(100% - 100px);
	min-width: calc(100% - 100px);

	font-family: 'Indie Flower', cursive;

	border: none;
}

.debounce-input:focus {
	outline: none;
}

.input-component:hover,
.input-component.withFocus {
	box-shadow: 0px 0px 0px 0px #718b93;
}

.input-component-clear-button {
	border: none;
	background-color: inherit;
	margin: 0 10px;
	transform: scale(1);
	transition: transform ease-in-out 0.3s;
}

.input-component-clear-button:hover,
.input-component-clear-button:focus {
	transform: scale(1.2);
}

.input-component-clear-button-icon {
	color: #323e42;
}
