.iconButtonComponent {
	display: block;
	width: 40px;
	height: 40px;
	margin-top: 5px !important;
	padding: 5px !important;
	color: #323e42 !important;
	border: 1px solid #323e42 !important;
	box-shadow: 5px 5px 5px -1px #718b93;
	background: #eff1db!important;

	transition: box-shadow ease-in-out 0.2s, color ease-in-out 0.2s !important;
}

.iconButtonComponent:hover,
.iconButtonComponent:focus {
	box-shadow: 0px 0px 0px 0px #718b93;
	color: #e0004d !important;
	border-color: #e0004d !important;
	background: #eff1db!important;
}
